<template>
  <!-- 外部容器-->
  <el-card class="editbox">
    <!-- 工具栏容器 -->
    <div id="toolbar-container"></div>
    <!-- 编辑器容器 -->
    <div id="editor"></div>
    <el-button type="primary" @click="showTodos">获取Todos</el-button>
    <el-button type="primary" @click="submitArticle">提交内容</el-button>
    <el-button type="primary" @click="getArticle">读取内容</el-button>
    <div v-html="content"></div>
  </el-card>
</template>
<script>
import { ref, reactive, onMounted } from "vue"
import { useStore } from "vuex"
import Editor from "@ckeditor/ckeditor5-build-decoupled-document"
// import CodeBlock from '@ckeditor/ckeditor5-code-block/src/codeblock.js';
import MyUploadAdapter from "./uploadImg"

export default {
  name: "CKEditor",
  setup() {
    // 创建一个编辑器对象数据
    let editorObj = reactive({})
    // 初始化编辑器
    const initCKEditor = () => {
      Editor.create(document.querySelector("#editor"), {
        language: "zh-cn",
        // plugins: [ CodeBlock ],
        // toolbar: [ 'codeBlock' ]
      })
        .then((editor) => {
          const toolbarContainer = document.querySelector("#toolbar-container")
          toolbarContainer.appendChild(editor.ui.view.toolbar.element)
          editorObj = editor //将编辑器保存起来，用来随时获取编辑器中的内容等，执行一些操作
          editor.plugins.get("FileRepository").createUploadAdapter = (loader) => {
            return new MyUploadAdapter(loader) //自定义上传图片
          }
        })
        .catch((error) => {
          console.error(error)
        })
    }
    // 获取编辑器数据
    const getEditorData = () => {
      return editorObj.getData()
    }
    // 设置编辑器数据
    const setEditorData = (data) => {
      return editorObj.setData(data)
    }
    //在mounted初始化
    onMounted(() => {
      initCKEditor()
    })
    // 定义输出到页面的内容
    let content = ref("")
    // 引入Vuex仓库store
    const store = useStore()
    const showTodos = () => {
      setEditorData(
        JSON.stringify(
          store.state.test.todos.reduce((res, todo) => (res += todo.title + "<br/>"), "")
        )
      )
      console.log("总字数 =", getEditorData().length)
    }
    const submitArticle = () => {
      store.commit("setArticle", getEditorData())
      content.value = getEditorData() //内容输出到页面
    }
    const getArticle = () => {
      setEditorData(store.state.test.article)
    }
    return {
      editorObj,
      getEditorData,
      setEditorData,
      showTodos,
      submitArticle,
      getArticle,
      content,
    }
  },
}
</script>
<style scope>
/* 编辑内容样式 */
/* @import url("./content-styles.css"); */
.editbox {
  width: 1250px;
  height: 1000px;
  margin: 0 auto; /*居中*/
  padding-bottom: 15px;
}
#editor {
  border: 2px, solid, rgb(184, 179, 179);
  background-color: aliceblue;
  height: 350px;
  margin-bottom: 10px;
}
</style>
